<template>
  <div class="head">
    <van-nav-bar :class="[{'whiteBg':isWhiteBg},{'active': headerActive}]" v-if="!isShowMenu" :title="title" left-text="">
      <template #left>
      </template>
      <template #right>
        <img class="back" :src="isWhiteBg?require('@/assets/images/closer@2x.png') : require('@/assets/images/close-white.png')" @click="closeWeb" alt="">
      </template>
    </van-nav-bar>
    <van-nav-bar v-if="isShowMenu" :title="title" left-text="" :class="{'active': headerActive}" left-arrow>
      <template #left>
        <img class="kwl-img" src="@/assets/images/logo.png" alt="">
