      </template>

    </van-nav-bar>
    <!-- 菜单栏 -->
    <div class="menu-block" v-show="open">
      <div class="user-info__tit flex-between">
        <div class="flex-middle">
          <img class="kwl-img" src="@/assets/images/logo.png" alt="">
