        </div>
        <div class="flex-middle">
          <div class="menu-close" @click="closeMenu()">
            <img src="@/assets/images/close.png" alt="">
          </div>
        </div>
      </div>
      <div class="menu-list">
        <a class="ui-footer__item flex-middle active" href="__APP__/DmsUser/Index/index">
          <div class="ui-footer__icon">
            <img src="@/assets/images/nav1.png" alt="">
            <img src="@/assets/images/nav1-a.png" alt="">
          </div>
          <div class="ui-footer__label">主页</div>
        </a>
        <div class="ui-footer__item flex-middle" @click="$router.push({name:'RecordsPower'})">
          <div class="ui-footer__icon">
            <img src="@/assets/images/nav4.png" alt="">
            <img src="@/assets/images/nav4-a.png" alt="">
          </div>
          <div class="ui-footer__label">实体算力明细</div>
        </div>
        <div class="ui-footer__item flex-middle" @click="$router.push({name:'RecordsOutput'})">
          <div class="ui-footer__icon">
            <img src="@/assets/images/nav5.png" alt="">
            <img src="@/assets/images/nav5-a.png" alt="">
          </div>
          <div class="ui-footer__label">NBTC产出明细</div>
        </div>
        <a class="ui-footer__item flex-middle" href="__APP__/DmsUser/FunBank/nbtcGetList">
          <div class="ui-footer__icon">
