            <img src="@/assets/images/nav2-a.png" alt="">
          </div>
          <div class="ui-footer__label">NBTC提取明细</div>
        </a>
        <div class="ui-footer__item flex-middle" @click="$router.push({name:'Team'})">
          <div class="ui-footer__icon">
            <img src="@/assets/images/nav6.png" alt="">
            <img src="@/assets/images/nav6-a.png" alt="">
          </div>
          <div class="ui-footer__label">我的好友</div>
        </div>
        <div class="ui-footer__item flex-middle" @click="$router.push({name:'Share'})">
          <div class="ui-footer__icon">
            <img src="@/assets/images/nav7.png" alt="">
            <img src="@/assets/images/nav7-a.png" alt="">
          </div>
          <div class="ui-footer__label">邀请</div>
        </div>
        <div class="ui-footer__item flex-between language" :class="{'up':isShowLang}" @click="isShowLang = !isShowLang">
          <div class="flex-middle">
            <div class="ui-footer__icon">
              <img src="@/assets/images/nav8.png" alt="">
              <img src="@/assets/images/nav8-a.png" alt="">
            </div>
            <div class="ui-footer__label">语言</div>
          </div>
          <div class="sel">
            <img src="@/assets/images/sel.png" alt="">
          </div>
        </div>
        <div class="lang-block" v-show="isShowLang">
          <div class="lang-item flex-middle" v-for="(item,index) in langList" :key="index" @click="onSelectLang(item)">
            <img class="lang-img" :src="item.icon" alt="">
            <div class="lang active">{{item.text}}</div>
          </div>
        </div>
        <div class="Invit-link">
          <div class="flex-between">
            <div class="title">编号</div>
            <img class="clip-menu-button" data-clipboard-action="copy" data-clipboard-target="#link" src="@/assets/images/copy.png" alt="">
          </div>
          <div class="link-address" id='link'>TNwg8sNuAQmv8zGBsoUENEjaKvAfoVnjLj</div>
        </div>
        <div class="btn logout" style="margin-top: 20px">退出登录</div>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, computed, watch, onMounted } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import web3Data from "@/utils/initWeb3";
import { getLang, setLang } from "@/utils/i18n";
import { onBeforeRouteLeave, useRouter } from 'vue-router'
import Clipboard from 'clipboard'
import { Toast } from 'vant';
export default {
  components: {
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    isShowMenu: {
      type: Boolean,
      default: false
    },
    isWhiteBg: {
      type: Boolean,
      default: true
    },
  },
  setup (props) {
    const router = useRouter()
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      open: false,
      headerActive: 0,
      title: props.title,
      local: getLang().toLowerCase() || "zh-cn",
      langList: [
        { text: '中文', lang: 'zh-cn', icon: require("@/assets/images/china.png") },
        { text: 'English', lang: 'en', icon: require("@/assets/images/english.png") }
      ],
      isShowLang: false,
      changeLanuage: (val) => {
        setLang(val)
        data.local = val
        location.reload()
      },
      initContract: () => {
        data.initData()
      },
      initData: () => {
      },
      initWallet: () => {
        web3Data.initWeb3()
      },
      onMenu: () => {
        let flag = !data.open
        data.open = flag
        document.querySelector('body').style.overflow = flag ? 'hidden' : 'auto'
      },
      openUrl: (url) => {
        window.location.href = url;
      },
      closeMenu: () => {
        document.querySelector('body').style.overflow = 'auto'
        data.open = false
      }
    })
    document.addEventListener('scroll', function () {
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      data.headerActive = scrollTop > 30
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const onSelectLang = (action) => {
      if (action.lang) {
        data.changeLanuage(action.lang)
      }
      data.showLang = false
    }
    const closeWeb = () => {
      router.replace({ name: 'Index' })
    }
    onMounted(() => {
      var clipboard = new Clipboard('.clip-menu-button')
      clipboard.on('success', function (e) {
        Toast(t('share.複製成功'))
        e.clearSelection()
      })
      clipboard.on('error', function () {
        Toast(t('share.複製失敗，請手動長按複製'))
      })
    })
    onBeforeRouteLeave(() => {
      document.querySelector('body').style.overflow = 'auto'
      data.open = false
    })
    const refData = toRefs(data);
    return {
      ...refData,
      onSelectLang,
      closeWeb
    };
  }
}
</script>
<style lang="less" scoped>
.wallet-block {
  border: 1px solid #ffffff;
  border-radius: 20px;
  padding: 4px 10px;
  width: 108px;
