    width: 19px;
    height: 19px;
  }
  .address {
    overflow: hidden;
    white-space: nowrap;
    font-size: 14px;
    color: #ffffff;
    margin-left: 10px;
  }
}

.kwl-img {
  width: 34px;
  height: 34px;
  margin-right: 10px;
}

.kwl-name {
  font-size: 18px;
  font-weight: bold;
  color: #ffffff;
}
.menu-block {
  position: fixed;
  left: 0;
