}
.menu-list {
  margin: 0 15px;

  .ui-footer__item {
    // height: 50px;
    justify-content: flex-start;
    flex-direction: row;
    margin: 10px 0 10px 0;
    padding: 0 0 0 20px;
    height: 40px;
    line-height: 40px;

    .ui-footer__icon {
      width: 20px;
